import React from 'react';
import { Outlet, useLocation } from 'react-router-dom';

import { Cards } from '../Cards';
import { CustomLink } from '../CustomLink';
import turning from '../../assets/img/turning.png';
import cutters from '../../assets/img/cutters.png';
import drill from '../../assets/img/drill.png';
import axial from '../../assets/img/axial.png';

import './products.scss';

function Products() {
  const { pathname } = useLocation();

  return (
    <>
      <section id='products' className='products'>
        <div className='container'>
          <h2 className='products__topTitle'></h2>
          <p className='products__topText'>
            MEGATEC - , ,  <br />
                .
          </p>

          <nav className='products__menu' id='prod_menu'>
            <CustomLink classNames='products__link' to='/products/turning/'>
              <div className='products__card'>
                <Cards src={turning} alt=' '>
                  <h3> </h3>
                </Cards>
              </div>
            </CustomLink>

            <CustomLink classNames='products__link' to='/products/cutters/'>
              <div className='products__card'>
                <Cards src={cutters} alt='   '>
                  <h3>   </h3>
                </Cards>
              </div>
            </CustomLink>

            <CustomLink classNames='products__link' to='/products/drill/'>
              <div className='products__card'>
                <Cards src={drill} alt='   '>
                  <h3>   </h3>
                </Cards>
              </div>
            </CustomLink>

            <CustomLink classNames='products__link' to='/products/axial/'>
              <div className='products__card'>
                <Cards src={axial} alt=' '>
                  <h3> </h3>
                </Cards>
              </div>
            </CustomLink>
          </nav>
          <div id='description' />
          {pathname === '/products' ? (
            <div className='products__addInfo'>
              <h3 className='products__addTitle'>  </h3>
              <p className='products__addText'>
                MEGATEC - , ,  <br />
                    .
              </p>
            </div>
          ) : (
            <Outlet />
          )}
        </div>
      </section>
    </>
  );
}

export default Products;
